@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
body {
  background-image: linear-gradient(to right bottom, #00f260, #0575e6);
  background-attachment: fixed;
  color: #354538;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(to right bottom, #00f260, #0575e6);
}

/* a {
  text-decoration: none;
  color: #354548;
}

a:hover {
  text-decoration: none;
} */

.app__container {
  margin: 30px;
}

.app__row {
  height: 80vh;
}

.app__main-content {
  background-color: #fff;
  border-radius: 20px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
}

/* sidebar */
.sidebar {
  background-color: #fff;
  height: 100%;
  border-radius: 20px;
  padding: 35px 15px;
  box-shadow: 0 0 10px black;
  text-align: center;
}

.usha_image {
  height: 160px;
  width: 160px;
  object-fit: contain;
  border-radius: 200%;
}

.sidebar__icon {
  width: 25px;
  height: 25px;
}

.sidebar__name {
  font-family: "Kaushan Script";
  margin: 20px 0;
  font-size: 2.5rem;
}

.sidebar__name > span {
  color: #09aa49;
}

.sidebar__item {
  padding-top: 6px;
  height: 50px;
  font-size: 1.5rem;
  border-radius: 30px;
}

.sidebar__title {
  background-color: #dbdddf;
  border-radius: 30px;
  margin: 45px 10px;
  font-weight: 600;
}

.sidebar__contact {
  background-color: #dbdddf;
  padding: 14px 5px;
  margin: 25px 5px 40px;
  border-radius: 60px;
}

.sidebar__location {
  font-size: 1.3rem;
  margin-bottom: 10px;
  height: 80px;
}

.sidebar__location p {
  font-size: 1.25rem;
  margin-top: 3px;
}
.sidebar__email {
  background-image: linear-gradient(to right bottom, #09aa49, #0575e6);
  width: 160px;
  border-radius: 30px;
  color: #fff;
  margin: 25px 25px 0px;
  cursor: pointer;
  transition: all 0.5s ease;
  margin: auto;
  font-size: 1.25rem;
}

.sidebar__email:hover {
  transform: scale(1.1);
}

@media (max-width: 991px) {
  .app__main-content {
    margin: 30px 0;
  }
}

/* navbar */
.navbar {
  background-color: #dbdddf;
  border-radius: 10px;
  height: 45px;
}

.navbar__items {
  display: flex;
}

.navbar__active {
  font-size: 1.4rem;
  font-weight: 600;
  color: #09aa49;

  margin-left: 10px;
}

.navbar__item {
  margin-right: 20px;
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  cursor: pointer;
  font-weight: 500;
  padding-bottom: 5px 0;
}

.navbar__item:hover {
  background: -webkit-linear-gradient(hsl(144, 87%, 53%), #0575e6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-decoration: none;
}

/* about */

.about {
  padding: 20px;
  margin: -35px;
}

.about__intro {
  font-size: 1.25rem;
  padding: 30px;
  margin: 10px 0;
  font-style: italic;
}

.about__container {
  background-color: #dbdddf;
  padding: 30px 20px;
}

.about__heading {
  font-size: 30px;
  font-weight: 600;
  color: #09aa49;
  margin-left: 15px;
  margin-bottom: 25px;
}

/* skill-card */
.skill-card {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  margin: 15px;
  padding: 10px;

  border-radius: 10px;
}

.skill-card__icon {
  width: 40px;
  height: 40px;
}

.skill-card-body {
  margin-left: 15px;
}

.skill_card__content {
  margin: 0;
  font-size: 1.1rem;
  color: black;
}

/* resume */

.resume .row {
  padding-top: 10px;
}

.resume-card {
  margin-right: 30px;
}

.resume-card__heading {
  margin-left: 5px;
  font-size: 28px;
  font-weight: 450;
  color: #09aa49;
  margin-top: 20px;
  margin-right: 20px;
}

.resume-language__heading {
  margin-left: 20px;
  font-size: 28px;
  font-weight: 450;
  color: #09aa49;
  margin-top: 20px;
}
.resume-card__body {
  margin-top: 30px;
  margin-left: 5px;
}

.resume-card__title {
  font-weight: 500;
  font-style: normal;
  font-size: 1.3rem;
}

.resume-card__name {
  font-size: 18px;
  margin-top: 10px;
  font-weight: 400;
}

.resume-card__details {
  font-size: 1.1rem;
  padding-top: 10px;
  font-weight: 500;
}
.resume-language__body {
  display: flex;
  justify-content: space-between;
  font-size: 1.115rem;
  font-weight: bold;
  margin: 25px 10px 10px 25px;
}

.lng div {
  margin: 10px;
  margin-left: -2px;
  cursor: pointer;
}
.lng div:hover {
  background: -webkit-linear-gradient(hsl(144, 87%, 53%), #0575e6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.lng1 div {
  margin: 10px;
  cursor: pointer;
}

.lng1 div:hover {
  background: -webkit-linear-gradient(hsl(144, 87%, 53%), #0575e6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* projects */
.projects {
  height: 80vh;
  padding-top: 25px;
  overflow-y: auto;
}

.projectCard__wrapper {
  margin: 20px 40px;
  width: 70%;
  height: 80%;
}

.projectCard__image {
  width: 100%;
  height: 80%;
  border-radius: 30px;
  border: 1px solid #09aa49;
  transition: transform 0.3s linear;
}

.projectCard__image:hover {
  transform: scale(1.05);
}

.projectCard__title {
  margin-top: 12px;
  font-size: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects__navbar {
  display: flex;

  justify-content: center;
}

.projects__navbar > div {
  margin: 10px;
  font-size: 1.25rem;
  cursor: pointer;
  color: #09aa49;
  font-weight: 600;
}

.projects__navbar > div:hover {
  background: -webkit-linear-gradient(hsl(144, 87%, 53%), #0575e6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.projectCard__title img {
  width: 30px;
  margin: 5px;
}

.projectCard__wrapper .picture {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.projectCard__title a {
  color: #09aa49;
  text-decoration: none;
  padding: 5px;
}

.projectCard__title a:hover {
  background: -webkit-linear-gradient(hsl(144, 87%, 53%), #0575e6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

